.search{

    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0px;
    margin: 0px;
    background: #FFFFFF;
    border: 1px solid #ECEBED;
    box-sizing: border-box;
    box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
    border-radius: 6px;
}
.search__default{
    width: 300px;
    height: 48px;

}
.search__icon{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px 8px 0px 12px;
    color:#D0C9D6;
}
.search-default__icon{
    width: 24px;
    height: 24px;
}

.search__text{
    border:none;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
    color: #3F3356;
    caret-color: #3F3356;
    flex-grow:1;
    text-overflow:ellipsis;
    opacity:1;


}

.search-default__text{
    width: 182px;
    height: 20px;
    font-size: 15px;
}
